@import '../../assets/variables';
.card {
    font-family: "Mont", Arial, Helvetica, sans-serif;
    transition: opacity 0.3s ease-in-out;

    display: flex;
    flex-direction: column;
    justify-content: center;

    width: 272px;
    height: 506px;
    //min-width: 70%;
    background-color: $gray-black-color-2;
    color: $white-color;

    padding: 32px;

    &__photo {
      width: 208px;
      height: 196px;
      margin: auto;
      object-fit: scale-down;
      transition: 0.1s ease-in-out;
      &:hover {
        transform: scale(1.1);
        cursor: pointer;
      }
    }

    &__name {
      margin: 24px 0 8px;
      font-weight: normal;
      font-size: 14px;
      line-height: 21px;
      height: 42px;
      color: $white-color;
    }

    &__divider {
      height: 2px;
      width: 100%;
      margin: 8px 0;
      background-color: $gray-color-3;
    }

    &__info {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      width: auto;
      margin: 4px 0;

      &--details {
        font-weight: bold;
        font-size: 12px;
        line-height: 20px;
        color: $gray-color-1;
        width: auto;
      }
      &__info--value {
        font-weight: bold;
        font-size: 12px;
        line-height: 16px;
        color: $white-color;
        width: auto;
      }
    }

    &__price {
      font-weight: bolder;
      font-size: 22px;
      line-height: 31px;
    }

    &__buttons{
      display: flex;
      justify-content: space-between;
      gap: 8px;

      font-weight: bold;
      text-transform: uppercase;
      text-align: center;
      font-size: 14px;
      line-height: 13px;
      color: $white-color;

      &-transparent {
        align-items: center;
        padding: 12px;
        flex-grow: 6;
        margin: 16px 0;
        color: transparent;
        text-overflow: collapse;
        background-color: transparent;
        border: 1px solid transparent;
        position: relative;
        user-select: none;
      }

      &-counter {
        position: absolute;
        top: calc(50% - 16px);
        left: calc(50% - 48px);
      }

      &-buy{
        align-items: center;
        padding: 12px;
        flex-grow: 6;
        margin: 16px 0;
        background-color: $accent;
        border: 1px solid transparent;

        &:hover {
          color: var(--light-blue);
          background-color: var(--white);
          border-color: var(--light-blue);
          cursor: pointer;
        }
      }

      &-fav{
        align-items: center;
        flex-grow: 1;
        margin: 16px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $gray-color-2;
        border: 1px solid transparent;
        user-select: none;

        &:hover {
          cursor: pointer;
          background-image: url('../../icons/Favourites\ \(Heart\ Like\).svg');
          background-repeat: no-repeat;
          background-position: center;

          img {
            opacity: 0;
          }
        }

        &:active {
          border: 1px solid $gray-color-2;
          background-color: $gray-color-3;
        }

        &--empty {
          &:hover {
            background-image: url('../../icons/Favourites\ Filled\ \(Heart\ Like\).svg');
          }
        }

        &--noHover {
          &:hover {
            background-image: none;

            img {
              opacity: 1;
            }
          }
        }
      }
    }

    &--phones-page {
      width: 80%;
      margin: 0 auto;

      @media(min-width: 640px) {
        margin: 0;
      }

      @media(min-width: 1200px) {
        width: 90%;
      }

      @media(min-width: 1600px) {
        width: 100%;
      }
    }
}
